@use 'variables' as v;
@use 'mixins' as m;

.right {
    display: inline-block;
    margin-left: auto;
}
.left {
    display: inline-block;
    margin-right: auto;
}
.center {
    text-align: center;
}
.justify {
    text-align: justify;
}
@each $d in v.$displays {
    .d-#{$d} {
        display: $d !important;
    }
}
@include m.respond(v.$smDevice) {
    @each $d in v.$displays {
        .d-sm-#{$d} {
            display: $d !important;
        }
    }
}
@include m.respond(v.$mdDevice) {
    @each $d in v.$displays {
        .d-md-#{$d} {
            display: $d !important;
        }
    }
}
@include m.respond(v.$lgDevice) {
    @each $d in v.$displays {
        .d-lg-#{$d} {
            display: $d !important;
        }
    }
}
@include m.respond(v.$xlDevice) {
    @each $d in v.$displays {
        .d-xl-#{$d} {
            display: $d !important;
        }
    }
}
@include m.respond(v.$xxlDevice) {
    @each $d in v.$displays {
        .d-xxl-#{$d} {
            display: $d !important;
        }
    }
}
.main {
    flex: 1;
}
.section {
    padding: 6rem 0 3rem 0;
    &.top {
        margin-top: 4rem;
    }
}
.display {
    @include m.responsive-font(v.$displayFontSize, v.$displayFontSizeMd, v.$displayFontSizeLg);
}
h1 {
    @include m.responsive-font(v.$h1FontSize, v.$h1FontSizeMd, v.$h1FontSizeLg);
}
h2 {
    @include m.responsive-font(v.$h2FontSize, v.$h2FontSizeMd, v.$h2FontSizeLg);
}
h3 {
    @include m.responsive-font(v.$h3FontSize, v.$h3FontSizeMd, v.$h3FontSizeLg);
}