@use 'base/variables' as v;
@use 'base/mixins' as m;

header {
    background-color: #fff;
    height: 8rem;
    display: flex;
    align-items: center;
    width: 100%;
    position: absolute;
    top: 0;
    left: 0;
    overflow: hidden;
    border-bottom: .1rem solid #e4e4e4;
    text-align: center;
    color: #191f3a;
}