@font-face {
    font-family: "Roboto";
    src: url("../fonts/Roboto-Regular.ttf") format("truetype");
    font-weight: 400;
    font-style: normal;
    font-display: swap;
}
@font-face {
    font-family: "Roboto";
    src: url("../fonts/Roboto-Medium.ttf") format("truetype");
    font-weight: 500;
    font-style: normal;
    font-display: swap;
}

// ** TYPOGRAPHY ** //
$primaryFont: "Roboto", sans-serif;

// ** BREAKPOINTS ** //
$smDevice: 576px;
$mdDevice: 768px;
$lgDevice: 992px;
$xlDevice: 1200px;
$xxlDevice: 1400px;

// ** COLORS ** //
$primaryColor: #6366f1;
$textColor: #141414;
$backgroundColor: #ffffff;

$successColor: #22c55e;
$dangerColor: #ef4444;
$warningColor: #f59e0b;
$infoColor: #3b82f6;

// ** DISPLAYS ** //
$displays: none, block, flex, grid, inline, inline-block;

// ** FONT SIZE ** //
$displayFontSize: 3.2rem;
$displayFontSizeMd: 4.4rem;
$displayFontSizeLg: 5.8rem;

$normalFontSize: 1.5rem;
$normalFontSizeMd: 1.55rem;
$normalFontSizeLg: 1.65rem;

$h1FontSize: 2.8rem;
$h1FontSizeMd: 3.2rem;
$h1FontSizeLg: 4rem;

$h2FontSize: 2.4rem;
$h2FontSizeMd: 2.8rem;
$h2FontSizeLg: 3.2rem;

$h3FontSize: 2rem;
$h3FontSizeMd: 2.4rem;
$h3FontSizeLg: 2.8rem;

$smallFontSize: 1.2rem;
$smallFontSizeMd: 1.3rem;
$smallFontSizeLg: 1.4rem;