@use 'variables' as v;
@use 'mixins' as m;

html {
    height: 100%;
    font-size: 62.5%;
    box-sizing: border-box;
    -webkit-tap-highlight-color: transparent;
}
*,*:before, *:after {
    box-sizing: inherit;
}
body {
    font-family: v.$primaryFont;
    line-height: 1.4;
    color: v.$textColor;
    background-color: v.$backgroundColor;
    font-weight: 400;
    height: 100%;
    margin: 0;
    display: flex;
    flex-direction: column;
    @include m.responsive-font(v.$normalFontSize, v.$normalFontSizeMd, v.$normalFontSizeLg);
    &::selection {
        color: v.$backgroundColor;
        background-color: v.$primaryColor;
    }
}
.container {
    width: 90%;
    max-width: 120rem;
    margin: 0 auto;
}
.container-fluid {
    width: 95%;
    margin: 0 auto;
}
a {
    text-decoration: none;
}
ul,li {
    list-style: none;
}
h1,h2,h3, p, ul, li {
    margin: 0;
    padding: 0;
}
button,
input,
textarea {
    outline: none;
    border: none;
}
button,
input[type="submit"],
input[type="reset"],
input[type="button"] {
    cursor: pointer;
}
img {
    width: 100%;
    display: block;
    object-position: center center;
    object-fit: cover;
}