@use 'base/variables' as v;
@use 'base/mixins' as m;

.main .container img {
    width: 25rem;
    margin: 15rem auto;
}
.map-container {
    width: 90%;
    margin: 0 auto;
    height: 40rem;
    overflow: hidden;
    border-radius: 1rem;
    margin-bottom: 3rem;
    iframe {
        width: 100%;
        height: 100%;
        border: 0;
    }
}