@use "sass:color";
@use 'variables' as v;

@mixin respond($breakpoint) {
    @media (min-width: $breakpoint) {
        @content;
    }
}
@mixin responsive-font($base, $md, $lg) {
    font-size: $base;
    @include respond(v.$mdDevice) {
        font-size: $md;
    }
    @include respond(v.$lgDevice) {
        font-size: $lg;
    }
}
@mixin grid($columns: 1, $gap: 1.5rem, $align: stretch){
    display: grid;
    grid-template-columns: repeat($columns, 1fr);
    gap: $gap;
    align-items: $align;
}
@mixin button($color, $bgColor, $radius){
    color: $color;
    background-color: $bgColor;
    display: inline-block;
    padding: 1.4rem 1.6rem;
    border-radius: $radius;
    height: 5rem;
    display: flex;
    align-items: center;
    justify-content: center;
    transition: all 0.3s ease;
}
@mixin badge($color, $bgColor){
    display: inline-block;
    color: $color;
    background-color: $bgColor;
    padding: 0.4rem 0.8rem;
    border-radius: .5rem;
    font-size: 1.2rem;
    font-weight: 600;
}